.header
	@apply pt-5 xl:px-15 px-4
	@apply fixed left-0 right-0 z-100 top-0
	@apply transition-all duration-300
	.header-wrapper
		@apply flex items-center justify-between bg-white rounded-2
		@apply xl:h-20 h-20 xl:px-10 px-4
		.header-logo
			@apply md:rem:w-[86px] w-[8rem] rem:h-[62px]
			@apply xl:absolute xl:left-2/4 xl:-translate-x-2/4
			a
				@apply w-full h-full block
				img
					@apply w-full h-full object-contain
		.header-left
			@apply xl:block hidden h-full
		.header-right
			@apply flex items-center gap-base h-full
			.header-right-inner
				@apply flex items-center gap-2
				.button-contact 
					@apply xl:inline-flex hidden
				.header-search
					@apply inline-flex items-center cursor-pointer
					@apply px-4 h-10 rounded-5
					i
						@apply font-normal text-Primary-2 body-1 pointer-events-none
		.header-language
			@apply relative
			&:hover
				.header-language-active
					&::after
						@apply rotate-180
				.header-language-list
					// c ngn ng th m ra li dng ny
					// @apply opacity-100 pointer-events-auto
					.wpml-ls
						@apply p-0
			.header-language-active
				@apply flex items-center
				@apply rem:text-[16px]
				ul
					>li
						>a
							@apply flex items-center gap-2 text-Primary-1 font-semibold
				&::after
					content: '\f0d7'
					@apply font-awesome font-bold ml-1 
					@apply transition-all duration-300 text-Primary-1
				li:not(.wpml-ls-current-language)
					@apply hidden
				.wpml-ls 
					@apply p-0
		.header-language-list
			@apply absolute top-full left-0
			@apply  text-white bg-Primary-1 
			@apply p-1 py-1 rem:text-[14px]
			@apply opacity-0 transition-300  pointer-events-none 
			// @apply shadow-dropshadow-light
			li
				a
					+hover-underline(95%, #fff)
				&.wpml-ls-current-language
					@apply hidden
	.header-menu
		@apply h-full flex
		@screen xl
			ul.header-nav
				@apply flex justify-center items-center gap-base
				>li							
					@apply relative h-full  
					@apply inline-flex flex-col justify-center
					a						
						@apply inline-flex items-center uppercase
						@apply rem:text-[18px] font-semibold text-Primary-1
					&:hover
						&>a
							@apply text-Primary-2
						&>ul
							@apply opacity-100 pointer-events-auto
					&[class*='current']
						&>a
							@apply text-Primary-2
					// &.menu-item-has-children
					// 	&>a
					// 		&::after
					// 			content: '\f107'
					// 			@apply font-awesome font-normal rem:text-[14px] ml-1
					&>ul
						@apply rem:min-w-[219px] bg-white p-4
						@apply top-full left-0 absolute flex flex-col gap-4
						@apply opacity-0 transition-all pointer-events-none
						&>li				
							a
								@apply rem:text-[18px] font-semibold inline-block text-Primary-1 uppercase hover:text-Primary-2
		@screen -xl
			height: calc(100dvh - var(--header-height))					
			@apply fixed top-[var(--header-height)] left-0 w-full  z-[100]
			@apply flex flex-col justify-between px-[15px] overflow-y-auto
			@apply opacity-0 translate-x-full transition-all duration-700 bg-white
			&>ul.header-nav
				@apply flex flex-col gap-3 mt-[26px]
				li
					@apply relative
					a
						@apply text-[18px] font-medium  block pb-0
					&.active
						.icon-arrow
							&::before
								@apply rotate-180
					.menu-item
						@apply flex items-center relative
					.icon-arrow
						@apply absolute top-0 right-0 h-full 
						@apply px-1 text-3xl font-normal
						@apply flex-center
						&::before
							content: '\f078'
							transform-origin: center center
							will-change: transform
							@apply font-awesome text-[20px] font-light ml-2
							@apply transition-300
					&.menu-item-has-children				
						&>a
							@apply relative 
							@apply flex items-center justify-between
							&.dropdown-active
								&::after
									@apply rotate-180
							&::after
								content: '\f0d7'
								@apply font-awesome font-black text-lg
						>ul
							background-color: rgb(212 192 161)
							@apply hidden
							>li
								>a
									@apply border-none p-[8px] 
								&[class*='current']
									&>a
										@apply text-black
		
	.header-bar
		@apply sq-12 flex-shrink-0 
		@apply items-center justify-center xl:hidden flex
		@apply  bg-Primary-1 text-3xl  text-white
		&.active
			i
				&::before
					content: "\f00d"
	.nav-mobile
		@apply w-[85vw] bg-white h-full
		@apply fixed top-0 right-0 z-[999] px-6
		@apply flex flex-col
		@apply transition-500 translate-x-full opacity-0 pointer-events-none
		.header-wrap-head
			@apply flex items-center justify-between
			@apply py-[calc(0.4166666667rem+1.0416666667rem)]
			.header-close
				@apply sq-10 bg-Primary-1 inline-flex items-center justify-center
				i
					@apply text-2xl text-white
		.header-wrap-menu
			@apply flex-1 overflow-y-auto
			ul.menu-mobile
				@apply flex flex-col gap-4
				>li
					a
						@apply block pt-2 pb-1 md:text-[24px] text-[20px]
						@apply border-b border-b-transparent px-2
					&[class*='current']
						.menu-item
							>a
								@apply text-Primary-2 border-b-Primary-2
					.menu-item
						@apply flex items-center relative
						.icon-arrow
							@apply absolute top-0 right-0 h-full flex-center px-3 text-3xl font-normal
							&::before
								content: '\f078'
								transform-origin: center center
								will-change: transform
								@apply font-awesome text-[20px] font-light
								@apply transition-300	
					&.active
						.menu-item
							.icon-arrow
								@apply rotate-180
					ul.sub-menu
						@apply hidden mx-4 bg-Primary-1
						li
							a
								@apply text-white text-[16px] py-2
							.menu-item
								.icon-arrow
									@apply rotate-0 text-white
							&.active
								.menu-item
									.icon-arrow
										@apply rotate-180
	&.active
		@extend .header-active
.header-active
	@apply p-0 shadow-Shadow-2 
	.header-wrapper
		@apply rounded-none
.header-overlay
	position: fixed
	opacity: 0
	transition: 0.3s all ease-in-out
	pointer-events: none
	top: 0
	left: 0
	width: 100%
	height: 100%
	background-color: rgba(0, 0, 0, 0.5)
	z-index: 50
body
	&.show-cart
		.header-overlay
			@apply opacity-100 pointer-events-auto

body.isOpenMenu
	.nav-mobile
		@apply opacity-100 translate-x-0 pointer-events-auto #{!important}
	.header-overlay
		@apply opacity-100 pointer-events-auto
body.isOpenSearch
	.header-search-form
		@apply opacity-100 pointer-events-auto translate-y-0
body
	&.product-detail-page, &.service-page, &.news-detail-page
		.global-breadcrumb
			@apply mt-[var(--header-height)]