.news
	.news-main
		.col-left
			.news-item
				@apply flex flex-col h-full
				.content
					@apply flex flex-col flex-1
				.title
					@apply heading-3 line-clamp-1
				.bottom
					.desc
						@apply line-clamp-2
				&:hover
					.content
						.bottom
							.title
								@apply text-Primary-1			
		.col-right
			@apply flex flex-col gap-6
			.news-item
				@apply border-none
				.title
					line-height: calc(32/24)
					@apply heading-3 line-clamp-2 
				.desc
					line-height: calc(22/16)
					@apply line-clamp-2
				.content
					@apply bg-transparent
					
				&:hover
					.content
						.bottom
							.title
								@apply text-Primary-1
	.news-list
		.news-item
			@apply grid-cols-1
			.content
				.bottom
					.title
						@apply line-clamp-2